/* ==========================================================================
   Centered With Breadcrumbs Title type style - begin
   ========================================================================== */

.mkd-title-holder {

	&.mkd-centered-with-breadcrumbs-type {

		.mkd-title-inner {
			text-align: center;
		}

		.mkd-page-title {
			margin: 0;
		}

		.mkd-page-subtitle {
			display: block;
			margin: 10px 0 0;
			box-sizing: border-box;
		}

		.mkd-breadcrumbs {
			color: $default-text-color;
			font-size: 13px;
			font-weight: 400;

			&.mkd-has-inline-style {

				a {
					opacity: 1;

					&:hover {
						color: inherit;
						opacity: 0.8;
					}
				}
			}

			a,
			span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				color: inherit;
			}

			a:hover {
				color: $default-heading-color;
			}
		}
	}
}

/* ==========================================================================
   Centered With Breadcrumbs Title type style - end
   ========================================================================== */